.intro {
  width: 100%;
  height: 100%;
  padding-top: 60px;
  padding-bottom: 50px;
  @include r($lg) {
    padding-top: 70px;
    padding-bottom: 40px; }
  @include r($md) {
    padding-bottom: 0; }
  @include r($xs) {
    padding-top: 55px;
    padding-bottom: 0; }
  @include r(370) {
    padding-top: 20px; }
  .swiper-container {
    width: 1600px;
    height: 100%;
    position: relative;
    @include r($md) {
      width: 1200px; }
    @include r($xs) {
      width: 100%; }
    .swiper-wrapper {
      width: 100%;
      height: 620px;
      @include r($lg) {
        height: 600px; }
      @include r($md) {
        height: 450px; }
      @include r($xs) {
        height: 100%;
        padding-bottom: 30px; }
      .swiper-slide {
        position: relative;
        display: flex;
        justify-content: space-between;
        width: 100%;
        @include r($xs) {
          flex-direction: column;
          align-items: center; }
        .intro__content {
          h2 {
            width: 270px;
            @include r($lg) {
              font-size: 50px;
              line-height: 55px; }
            @include r(1100) {
              font-size: 30px;
              line-height: 35px; }
            @include r($xs) {
              width: 100%;
              max-width: 270px; }
            @include r(370) {
              font-size: 20px; } }
          p {
            width: 535px;
            font-size: 28px;
            letter-spacing: 2.8px;
            font-weight: normal;
            line-height: normal;
            color: $dark-gray;
            padding-top: 35px;
            @include r($lg) {
              font-size: 25px;
              letter-spacing: 2.5px;
              width: 480px;
              padding-top: 30px; }
            @include r($md) {
              font-size: 20px;
              letter-spacing: 2px;
              width: 381px;
              padding-top: 25px; }
            @include r(1100) {
              font-size: 18px;
              line-height: 25px; }
            @include r($xs) {
              width: 100%;
              font-size: 16px;
              letter-spacing: 1.6px;
              line-height: 22px;
              max-width: 381px; }
            @include r(370) {
              font-size: 14px;
              letter-spacing: 1.4px; } }
          .content__bottom {
            display: flex;
            padding-top: 60px;
            @include r($lg) {
              padding-top: 50px; }
            @include r(1100) {
              flex-direction: column; }
            @include r($xs) {
              display: none; }
            .btn-primary {
              @include r(1100) {
                margin-bottom: 15px; } }
            .btn-next--blue {
              @include r(1100) {
                margin-left: 0; } } } }
        .intro__bottom {
          display: none;
          @include r($xs) {
            display: flex;
            justify-content: flex-start;
            width: 100%;
            .btn-next--blue {
              width: 100%;
              max-width: 290px;
              margin-left: 0; } } }
        .intro__image {
          width: 650px;
          height: 501px;
          margin-left: 50px;
          margin-right: 40px;
          margin-top: 25px;
          position: relative;
          @include r($lg) {
            width: 530px;
            height: 330px; }
          @include r($md) {
            width: 438px;
            height: 338px; }
          @include r(1100) {
            margin-left: 0; }
          @include r(999) {
            display: flex; }
          @include r($xs) {
            width: 294px;
            height: 227px;
            margin: 75px auto 155px; }
          @include r(410) {
            margin:  45px auto 125px; }
          @include r(370) {
            width: 230px;
            margin-bottom: 85px; }
          .btn-play {
            right: -60px;
            @include r($lg) {
              right: -80px; }
            @include r(999) {
              right: -30px; }
            @include r($xs) {
              right: -40px; }
            @include r(370) {
              right: -60px; } }
          img {
            width: 100%;
            object-fit: contain; } }
        .intro__card {
          display: flex;
          flex-direction: column;
          align-items: flex-start;
          width: 225px;
          height: 160px;
          border-radius: 6px;
          box-shadow: 0 15px 29px 0 rgba(201, 210, 226, 0.16);
          background-color: rgba(255, 255, 255, 0.65);
          padding: 210px 0 35px;
          margin-top: 180px;
          margin-right: 20px;
          position: relative;
          @include r($lg) {
            height: 140px; }
          @include r($md) {
            width: 160px;
            padding: 157px 0 25px;
            margin-top: 140px; }
          @include r(1199) {
            margin-top: 80px; }
          @include r(999) {
            display: none; }
          .card__content {
            p {
              font-size: 15.5px;
              padding-left: 50px;
              font-weight: 500;
              line-height: normal;
              letter-spacing: normal;
              color: #45464b;
              margin-bottom: 15px;
              @include r($lg) {
                font-size: 14px;
                padding-left: 40px; }
              @include r($md) {
                font-size: 11px;
                padding-left: 30px; } } }
          .btn-intro {
            padding-left: 50px;
            @include r($lg) {
              padding-left: 40px; }
            @include r($md) {
              padding-left: 30px; } }
          .card__image {
            position: absolute;
            top: 25px;
            left: -30px;
            width: 260px;
            height: 145px;
            @include r($lg) {
              width: 250px;
              height: 109px; }
            @include r($md) {
              width: 197px;
              height: 109px; }
            @include r(1199) {
              width: 180px; }
            img {
              width: 100%;
              object-fit: contain; } } }
        &:nth-of-type(2) {
          .intro__image {
            margin-top: 110px;
            margin-left: 0;
            width: 641px;
            height: 379px;
            //+r($lg)
            //width: 456px
            //height: 270px
            //margin-top: 90px
            @include r($md) {
              width: 456px;
              height: 270px;
              margin-top: 90px; }
            @include r($xs) {
              width: 280px;
              height: 175px;
              margin: 115px auto 150px; }
            @include r(410) {
              margin: 55px auto 125px; }
            @include r(370) {
              width: 230px;
              height: 136px; }
            .btn-play {
              top: -100px;
              @include r($md) {
                top: -70px; }
              @include r($xs) {
                top: -40px; } } }
          .intro__card {
            padding-top: 200px;
            margin-top: 210px;
            @include r($md) {
              padding-top: 167px;
              margin-top: 110px; }
            .card__image {
              width: 223px;
              height: 176px;
              top: 15px;
              left: -15px;
              @include r($lg) {
                width: 195px;
                height: 155px; }
              @include r($md) {
                width: 160px;
                height: 127px; } } } } } }
    .swiper-pagination {
      margin-top: 15px;
      bottom: 80px;
      left: 0;
      text-align: left;
      width: 300px;
      @include r($lg) {
        bottom: 140px; }
      @include r($md) {
        bottom: 30px; }
      @include r(1100) {
        bottom: 0; }
      @include r($xs) {
        bottom: 0; } }
    .swiper-pagination-bullet {
      width: 84px;
      height: 4px;
      border-radius: 2px;
      background-color: #c4d2ec;
      margin-right: 15px;
      @include r($lg) {
        width: 74px;
        border-radius: 1.8px; }
      @include r($md) {
        width: 60px;
        height: 3px;
        border-radius: 1.5px; } } } }
