.contacts {
  background-color: #f7f9fc;
  padding-bottom: 130px;
  @include r($lg) {
    padding-bottom: 115px; }
  @include r($md) {
    padding-bottom: 95px; }
  @include r($xs) {
    padding-bottom: 0; }
  .container {
    flex-direction: column;
    h2 {
      @include r($xs) {
        font-size: 24px;
        font-weight: normal;
        letter-spacing: 2.4px;
        color: #2b2c30; } } }
  .contacts__content {
    display: flex;
    justify-content: space-between;
    padding-top: 55px;
    @include r($md) {
      padding-top: 40px; }
    @include r(1100) {
      flex-direction: column-reverse; }
    ul {
      @include r(1100) {
        margin-top: 35px; }
      li {
        max-width: 590px;
        padding-left: 50px;
        position: relative;
        margin-right: 65px;
        margin-bottom: 50px;
        @include r($lg) {
          max-width: 520px;
          margin-bottom: 35px; }
        @include r($md) {
          max-width: 450px;
          margin-bottom: 35px; }
        @include r(1100) {
          max-width: 100%; }
        @include r($xs) {
          padding-left: 0; }
        @include r(414) {
          margin-right: 0;
          max-width: 290px; }
        .contacts-heading {
          display: flex;
          margin-left: -50px;
          position: relative;
          @include r($xs) {
            margin-left: 0; }
          h3 {
            font-size: 22.5px;
            font-weight: bold;
            color: #4a4a4a;
            padding-left: 50px;
            @include r($lg) {
              font-size: 20px; }
            @include r($md) {
              font-size: 16px; }
            @include r($xs) {
              padding-left: 20px; } } }
        p {
          font-size: 22.5px;
          font-weight: 500;
          line-height: normal;
          letter-spacing: normal;
          color: #4a4a4a;
          margin-top: 20px;
          @include r($lg) {
            font-size: 20px; }
          @include r($md) {
            font-size: 16px; }
          &:nth-of-type(3) {
            margin-top: 0;
            @include r($xs) {
              margin-top: 20px; } }
          span {
            color: #2468eb; } } } }
    .maps {
      @include r(1100) {
        display: flex;
        justify-content: center; }
      #map {
        width: 930px;
        height: 648px;
        @include r($lg) {
          width: 812px;
          height: 567px; }
        @include r($md) {
          width: 660px;
          height: 460px; }
        @include r(1100) {
          width: 100%; }
        @include r($xs) {
          width: 100%;
          height: 330px; } } } } }
